export default {
	data() {
		return {
			loginIndex: false,
		}
	},
	methods: {
		login() {
			var _this = this;
			if (_this.loginIndex) {
				_this.loginIndex = false;
			} else {
				_this.loginIndex = true;
			}
		},

		// 会员注册
		memberLogin() {
			uni.navigateTo({
				url: 'member_login',
			});
		},

		// 员工通道
		staffLogin() {
			uni.navigateTo({
				url: 'staff_login',
			});
		},
		
		// 商户注册
		merchantRegister() {
			uni.navigateTo({
				url: 'merchant_register',
			});
		}
	}
}
